import { defaultInstance } from '../InstanceAPI';

//  
export const verifyEmail = async (userEmail) => {
  try {
    const res = await defaultInstance.post(`/user/emailvalid`, userEmail);
    const message = res.data.message;

    if (message === '    .') {
      return `*${message}`;
    } else if (message === ' .' || !userEmail.user.email) {
      return '*   .';
    } else {
      return '';
    }
  } catch (error) {
    if (error.response) {
      const { status, data } = error.response;
      if (status === 422 && data.message === '  .') {
        return `*${data.message}`;
      } else {
        console.log(data.message);
        return null;
      }
    }
  }
};

//  
export const verifyAccountName = async (userAccountName) => {
  try {
    const res = await defaultInstance.post(`/user/accountnamevalid`, userAccountName);
    const message = res.data.message;
    const pattern = /^[A-Za-z0-9._]+$/;

    if (message === ' .' || userAccountName.user.accountname.length < 2) {
      return '* ID 2  .';
    } else if (message === '  ID .') {
      return `*${message}`;
    } else if (!pattern.test(userAccountName.user.accountname)) {
      return '* ID , , ,    .';
    } else {
      return '';
    }
  } catch (error) {
    if (error.response) {
      const { status, data } = error.response;
      if (status === 422 || status === 404) {
        console.log(data.message);
      }
    }
  }
};

// 
export const signUp = async (userData) => {
  try {
    const res = await defaultInstance.post(`/user`, userData);
    return res.data;
  } catch (error) {
    if (error.response) {
      const { status, data } = error.response;
      if (status === 422) {
        return `*${data.message}`;
      } else {
        console.log(data.message);
        return null;
      }
    }
  }
};

// 
export const login = async (userData) => {
  try {
    const res = await defaultInstance.post(`/user/login`, userData);
    return [res.data, null];
  } catch (error) {
    if (error.response) {
      const { status, data } = error.response;
      if (status === 422) {
        return [null, data];
      } else {
        console.log(data);
        return null;
      }
    }
  }
};